@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");
#intro_actividad {
  width: 100%;
  height: 60rem;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row-reverse;
  background: #129a63;
}
#intro_actividad #vineta {
  width: 60rem;
  background: #ffffff;
  padding: 2.4rem;
  border-radius: 2rem;
  border: 5px solid #3b5895;
  font-size: 2.4rem;
  text-align: center;
  position: absolute;
  top: 6rem;
  left: 20rem;
}
#intro_actividad #vineta #relativo {
  position: relative;
}
#intro_actividad #vineta #relativo #frase_01 {
  color: #3b5895;
  font-weight: 700;
}
#intro_actividad #vineta #relativo #frase_02 {
  color: #129a63;
  font-weight: 300;
}
#intro_actividad #vineta #relativo::after {
  content: "";
  position: absolute;
  background: #3b5895;
  top: 50%;
  right: -5.6rem;
  width: 3rem;
  height: 4rem;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}
#intro_actividad .cont-imagen {
  width: 40%;
  height: 100%;
  padding: 2rem 2rem 0 2rem;
  background: #616a84;
}